.section-1{
     width: 100%;
    display: block;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.section-1 .img-background {
    position: absolute;
    top: 0; left: 0;
    z-index: -5;
    height: 100%; width: 100%;
    background-image: url("../../public/img/justice-woman.png");
    transform: scaleX(-1);
    background-repeat: no-repeat;
    background-size: 120%;
    margin-top: 6rem;
    filter: opacity(34%);
    
}
.section-1 .banner-1 .logo {
    width: 100%; max-height: 25%;
}
.section-1 .banner-1 .logo img{
    max-width: 100%;
    height: 12rem;
}

.section-1 .banner-1 {
    padding: 6rem 0.5rem 0;

    width: 100%; margin-bottom: 3rem;
    display: flex; flex-direction: column;
    align-items: center;
   
    
}
.section-1 .banner-1 .icons {
    width: 100%; height: 10%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 8rem;
}
.section-1 .banner-1 .icons .icon-container img{
    max-width: 4rem;
}

.section-1 .banner-1 .title-1 {
    height: 40%;
    margin: 3rem 1.5rem;
    
    margin-bottom: 1rem;
}
.section-1 .banner-1 .title-1 h2 {
    font-family: 'Big Shoulders Display', cursive;
    color: #F25D40;
    font-weight: 400;
    line-height: 1;
    font-size: 58px;
    padding: 0 0.5rem;
    text-align: start;
}
.section-1 .banner-1 .title-1 p {
    display: none;
}
.section-1 .banner-1 .hr {
    width: 80%; 
    margin-left: auto; margin-right: auto;
    margin: 0 1rem;
    height: 3px;
    background-color: #F25D40;
    margin-bottom: 1rem;
}

.section-1 .banner-1 .arrow-icon-link {
    text-decoration: none;
    color: antiquewhite;
}
.section-1 .banner-1 .arrow-icon-link .arrow-icon {
    
    font-size: 2.5rem;
}
.section-1 .banner-1 .arrow-icon:hover {
    font-size: 3.2rem;
}


.section-1 .banner-2 {
    
    
    padding: 8rem 1.5rem 0;
    text-align: center;

    margin-bottom: 4rem;

}
.section-1 .banner-2 h2 {
    
    font-size: 42px;
    font-weight: 300;
    color: #F25D40;
    font-family: 'Big Shoulders Display', cursive;
    line-height: 0.9;
    letter-spacing: 1px;
    
    margin-bottom: 3rem;
    
}

.section-1 .banner-2 .info-container {
    height: 100%;
    margin-bottom: 8rem;
}
.section-1 .banner-2 .info-container .info {
    margin-bottom: 4rem;
}
.section-1 .banner-2 .info-container .info .info-title {
    display: flex; align-items: center; justify-content: center;
}
.section-1 .banner-2 .info-container .info .info-title h2 {
    font-size: 36px;
    font-weight: 400;
    color: antiquewhite;
    font-family: 'Big Shoulders Display', cursive;
    line-height: 0.9;
    letter-spacing: 1px;
    text-align: start;
    margin: 0;
    padding: 0 1rem;
}

.section-1 .banner-2 .info-container .info .info-title .info-icon {
    font-size: 3.5rem;
    color: #F25D40;
    margin-right: 1rem;

}
.section-1 .banner-2 .info-container .info .text p {
        color: antiquewhite;
        margin-top: 2rem;
        text-align: start;
        font-size: 20px;
        font-weight: 300;
        line-height: 1.2;
        letter-spacing: 1.2px;
}

.section-1 .banner-3 {
    width: 100%; 
    height: 100%;
    margin-bottom: 4rem;
}
.section-1 .banner-3 .info-container {
    display: flex; align-items: center; justify-content: center;
    height: 75vh;

    background-image: url('../../public/img/img-background-2.png');
    background-position: center;
    background-size: cover;
    

}


.section-1 .banner-3 .info-container h2 {
    
    position: absolute;
    
    color: rgb(238, 238, 238);
    
    font-weight: 300;
    line-height: 1;
    letter-spacing: 1px;
    font-size: 24px;
    padding: 0 1rem;
    text-align: start;

    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.692);
    text-align: center;
}
@media only screen and (max-width: 360px){
    .section-1 .banner-1 .title-1{
        margin-bottom: 0.5rem;
    }
    .section-1 .banner-1 .title-1 h2 {
        font-size: 48px;
    }
    .section-1 .banner-1 .icons {
        margin-bottom: 3.5rem;
    }

    .section-1 .banner-2 {
    }

    .section-1 .banner-3 .info-container h2 {
        font-size: 22px; font-weight: 200;
    }
    
}

@media only screen and (min-width: 1080px) {
    .section-1 .img-background {
        position: absolute;
        top: 0; left: 0;
        z-index: -5;
        height: 120%; width: 100%;
        background-image: url("../../public/img/justice-woman-desk.png");
        transform: scaleX(1);
        background-repeat: no-repeat;
        background-size: 80%;
    
        filter: opacity(25%);
    
    }
    .section-1 .banner-1 {
        height: 120vh;
        flex-direction: row; 
        align-items: center; justify-content: center;
        margin-left: auto; margin-right: auto;
    }
    .section-1 .banner-1 .logo {
        width: 100%; height: auto;
    }
    .section-1 .banner-1 .logo img{
        max-width: 100%;
        height: 14rem;
    }
    
    .section-1 .banner-1 .hr {
        width: 3px; height: 40%; 
        margin-left: auto; margin-right: auto;
        margin: 0 1rem;
        margin-right: 1rem;
        
        background-color: #F25D40;
        margin-bottom: 15rem;
    }
    .section-1 .banner-1 .icons {
        width: 100%; height: 10%;
       
        position: absolute;
        bottom: 0;
        margin-bottom: 2rem;
    }
    .section-1 .banner-1 .icons .icon-container img{
        max-width: 4rem;
    }
    .section-1 .banner-1 .logo{
        margin-bottom: 15rem;
    }
    .section-1 .banner-1 .logo img {
        height: 15rem;
    }

    .section-1 .banner-1 .arrow-icon-link{
        display: none;
    }

    .section-1 .banner-1 .title-1 {
        width: 80%;
        align-items: flex-start;
        padding: 0;
        margin-bottom: 17rem;
    }
    .section-1 .banner-1 .title-1 h2 {
        width: 70%;
        font-size: 38px;
        align-items: flex-end;
        font-weight: 400;
        margin-bottom: 1rem;
        color: #F25D40;
        text-align: start;
        padding: 0;
    }
    .section-1 .banner-1 .title-1 p {
        display: block;
        text-align: start;
        font-size: 16px;
        font-weight: 300;
        line-height: 1.2;
        letter-spacing: 1.2px;
    }

    .section-1 .banner-2 {
        
        padding: 7rem 2.5rem 0;
        height: 100vh;
        margin-top: 0;
        text-align: center;
    
    }
    .section-1 .banner-2 h2 {
        font-size: 42px;
        font-weight: 400;
        padding: 0 25% 2rem;

    }
    .section-1 .banner-2 .info-container {
        display: flex; align-items: flex-start; justify-content: center;
    }
    .section-1 .banner-2 .info-container .info {
        width: 33%; 
        padding: 0 1.5rem;
    }
    
    .section-1 .banner-2 .info-container .info .info-title {
        height: 2.5rem;
    }
    .section-1 .banner-2 .info-container .info .info-title h2 {
        font-size: 32px;
        width: 75%;
        
    }
    
    .section-1 .banner-2 .info-container .info .info-title .info-icon {
        font-size: 2.5rem;
        
    
    }
    .section-1 .banner-2 .info-container .info .text p {
            
            font-size: 16px;
          
    }

    .section-1 .banner-3 {
        width: 100%; height: 75vh;
        
    }
    .section-1 .banner-3 .info-container {

        height: 75%;

    }
    .section-1 .banner-3 .info-container h2 {
    
        
        font-weight: 400;
      
        letter-spacing: 1px;
        font-size: 24px;
        padding: 0 0.5rem;
    
        width: 50%;

      
        text-align: center;
    }
}